    <v-toolbar fixed app color="amber">
      <v-toolbar-side-icon dark @click="leftDrawer = !leftDrawer"></v-toolbar-side-icon>
      <v-toolbar-title v-text="title" class="white--text"></v-toolbar-title>
      <v-spacer/>
