        </v-badge>
        <v-icon>filter_list</v-icon> Filters
      </v-btn>
    </v-toolbar>
    <v-content>
      <v-container fluid class="text-xs-center" style="height: 100%;">

        <v-layout align-center justify-center column fill-height v-if="!loaded">
        <v-progress-circular
          :size="100"
          color="amber"
          indeterminate
        ></v-progress-circular>
        </v-layout>

        <v-slide-y-transition mode="out-in">
          <v-layout align-center>
            <v-flex lg1> </v-flex>
            <v-flex xs12 md12 lg10 xl8 style="z-index: 1">

            <v-data-table
              :headers="headers"
              :items="filteredTable"
              class="elevation-1"
              hide-actions
              item-key="ID"
              v-if="loaded"
            >
            <v-progress-linear slot="progress" color="blue" indeterminate></v-progress-linear>
              <template slot="items" slot-scope="props">
                <tr @click="props.expanded = !props.expanded">
                  <td><div class="text-xs-left" >
                    <v-flex style="position: relative">
                      <v-btn v-model="pictureToDisplay" flat class="amber--text" style="height: 50px; width: 100%; position:absolute; bottom: 0; right: -3px;"
                      @click.stop="$set(imageDialog, props.item.ID, true)"
                      >
                    </v-btn>
                      <v-dialog
                        v-model="imageDialog[props.item.ID]"
                        width="800px"
                        >
                        <v-card>
                          <v-img :src="'static/img/chara/' + props.item.ID + '.png'">
                          </v-img>
                          <v-card-actions>
                          <v-spacer></v-spacer>
                          <v-btn
                            color="primary"
                            flat
                            @click.stop="$set(imageDialog, props.item.ID, false)"
                          >
                            close
                          </v-btn>
                          </v-card-actions>
                        </v-card>
                      </v-dialog>

                      <img :src="'static/img/chara/1_' + props.item.ID + '.png'" height="50" class="mt-1"/>
                    </v-flex>
                    <v-flex>
                      <img :src="'static/img/filter/icon_rarity_star_' + props.item.rarity + '.png'" height="15"/>
                    </v-flex>
                  </div></td>
                  <td class="text-xs-left">
                    <v-layout row>
                      <v-flex>
                        <h4>{{ props.item.subname }}</h4>
                      </v-flex>
                    </v-layout>
                    <v-layout row>
                      <v-flex>
                        <h5 style="font-weight: normal;">{{ props.item.charaname }}</h5>
                      </v-flex>
                    </v-layout>


                  </td>
                  <td class="text-xs-left">
                      <img :src="'static/img/filter/' + props.item.school + '.png'" height="40" class="mt-1"/></td>
                  <td class="text-xs-left">
                      <img :src="'static/img/filter/' + props.item.element + '.png'" height="25" class="mt-2"/></td>
                  <td class="text-xs-left">
                      <img :src="'static/img/filter/' + props.item.atktype + '.png'" height="25" class="mt-2"/>
                  </td>
                  <td class="text-xs-left">
                      <img :src="'static/img/filter/' + props.item.position + '.png'" height="25" class="mt-2"/>
                  </td>
                  <td class="text-xs-left">{{ props.item.overall }}</td>
                  <td class="text-xs-left">{{ props.item.hp }}</td>
                  <td class="text-xs-left">{{ props.item.atk }}</td>
                  <td class="text-xs-left">{{ props.item.def }}</td>
                  <td class="text-xs-left">{{ props.item.mdef }}</td>
                  <td class="text-xs-left">{{ props.item.spd }}</td>

              </tr>
              </template>
              <template slot="no-data">
                <v-alert :value="true" color="error" icon="warning">
                  <v-layout row>
                    <v-flex xs3>
                      <img :src="'static/img/noresults.png'" height="150"/>
                    </v-flex>

                    <v-flex xs9>
                      <v-layout align-start justify-center column fill-height>
                        <v-spacer/>
                        <v-flex>
                          <h2 class="mt-4">No results found...</h2>
                        </v-flex>
                        <v-spacer/>
                      </v-layout>
                    </v-flex>
                  </v-layout>
                  <v-layout row>
                    <v-flex xs12 v-if="apiError">
                      <h2> API ERROR: {{ apiErrorResponse }} </h2>
                    </v-flex>
                  </v-layout>
                </v-alert>
              </template>

              <template slot="expand" slot-scope="props">
                <v-card color="grey lighten-2 py-2" flat>
                  <v-layout row>
                    <v-flex xs1 class="text-xs-center white--text mx-2">
                      <div class="mt-2" style="max-width: 100px; background-color: grey; border-radius: 25px;"><h4 class="">Skills</h4></div>
                    </v-flex>
                    <v-flex xs4 md3 class="mx-1" style="border-style: outset; border-width: 2px; border-color: #E0E0E0; border-radius: 12px;">
                      <v-layout column style="height: 100%">
                        <v-flex xs12 style="background-color: #EEEEEE; border-radius: 12px 12px 0px 0px;" >
                          <v-card class="white--text" style="border-radius: 12px 12px 0px 0px;">
                            <v-layout>
                              <v-flex xs8 class="text-xs-left" style="background-color: #FF5156; border-radius: 12px 0px 30px 0px;">
                                <h4 class="ml-3 pt-1">{{ props.item.skill1name }}</h4>
                              </v-flex>
                              <v-spacer/>
                              <v-flex xs2 class="text-xs-center my-1 mr-2">
                                <div style="background-color: grey; border-radius: 25px;"><h4 class="">AP 2</h4></div>
                              </v-flex>
                            </v-layout>
                          </v-card>
                        </v-flex>
                        <v-flex xs12>
                          <v-card flat height="100%" color="grey lighten-3" class="grey--text text--darken-1 pb-2" style="border-radius: 0px 0px 12px 12px ;">
                            <v-layout row>
                              <v-flex xs12 class="text-xs-left mt-2">
                                <h4 class="ml-3">{{ props.item.skill1effect }}</h4>
                              </v-flex>
                            </v-layout>
                          </v-card>
                        </v-flex>
                    </v-layout>
                    </v-flex>

                    <v-flex xs4 md3 class="mx-1" style="border-style: outset; border-width: 2px; border-color: #E0E0E0; border-radius: 12px;">
                      <v-layout column style="height: 100%">
                        <v-flex xs12 style="background-color: #EEEEEE; border-radius: 12px 12px 0px 0px;" >
                          <v-card class="white--text" style="border-radius: 12px 12px 0px 0px;">
                            <v-layout>
                              <v-flex xs8 class="text-xs-left" style="background-color: #FF5156; border-radius: 12px 0px 30px 0px;">
                                <h4 class="ml-3 pt-1">{{ props.item.skill2name }}</h4>
                              </v-flex>
                              <v-spacer/>
                              <v-flex xs2 class="text-xs-center my-1 mr-2">
                                <div style="background-color: grey; border-radius: 25px;"><h4 class="">AP 3</h4></div>
                              </v-flex>
                            </v-layout>
                          </v-card>
                        </v-flex>
                        <v-flex xs12>
                          <v-card flat height="100%" color="grey lighten-3" class="grey--text text--darken-1 pb-2" style="border-radius: 0px 0px 12px 12px ;">
                            <v-layout row>
                              <v-flex xs12 class="text-xs-left mt-2">
                                <h4 class="ml-3">{{ props.item.skill2effect }}</h4>
                              </v-flex>
                            </v-layout>
                          </v-card>
                        </v-flex>
                    </v-layout>
                    </v-flex>

                    <v-flex xs4 md3 class="mx-1" style="border-style: outset; border-width: 2px; border-color: #E0E0E0; border-radius: 12px;">
                      <v-layout column style="height: 100%">
                        <v-flex xs12 style="background-color: #EEEEEE; border-radius: 12px 12px 0px 0px;" >
                          <v-card  class="white--text" style="border-radius: 12px 12px 0px 0px;">
                            <v-layout>
                              <v-flex xs8 class="text-xs-left" style="background: rgb(255,81,86); background: linear-gradient(90deg, rgba(255,81,86,1) 0%, rgba(255,101,136,1) 10%, rgba(255,149,254,1) 37%, rgba(166,94,180,1) 67%, rgba(69,35,101,1) 100%); border-radius: 12px 0px 30px 0px;">
                                <h4 class="ml-3 pt-1">{{ props.item.climaxname }}</h4>
                              </v-flex>
                              <v-spacer/>
                              <v-flex xs2 class="text-xs-center my-1 mr-2">
                                <div style="background-color: grey; border-radius: 25px;"><h4 class="">AP 2</h4></div>
                              </v-flex>
                            </v-layout>
                          </v-card>
                        </v-flex>
                        <v-flex xs12>
                          <v-card flat color="grey lighten-3" class="grey--text text--darken-1 pb-2" height="100%" style="border-radius: 0px 0px 12px 12px ;">
                            <v-layout row>
                              <v-flex xs12 class="text-xs-left mt-2">
                                <h4 class="ml-3">{{ props.item.climaxeffect }}</h4>
                              </v-flex>
                            </v-layout>
                          </v-card>
                        </v-flex>
                    </v-layout>
                    </v-flex>

                    <v-spacer/>
                  </v-layout>

                  <v-layout row class="pb-2">
                    <v-flex xs1 class="text-xs-center white--text mx-2">
                      <div class="mt-2" style="max-width: 100px; background-color: grey; border-radius: 25px;"><h4 class="">Passives</h4></div>
                    </v-flex>
                    <v-flex xs3 class="text-xs-center white--text mx-2">
                      <div class="mt-2" style="background-color: #FF5156; border-radius: 10px;"><h4 class="">{{ props.item.autoskill1 }}</h4></div>
                    </v-flex>
                    <v-flex xs3 class="text-xs-center white--text mx-2">
                      <div class="mt-2" style="background-color: #FF5156; border-radius: 10px;"><h4 class="">{{ props.item.autoskill2 }}</h4></div>
                    </v-flex>

                    <v-spacer/>
                  </v-layout>

                  <v-layout row class="pb-2">
                    <v-flex xs1 class="text-xs-center white--text mx-2">
                      <div class="mt-2" style="max-width: 100px; background-color: grey; border-radius: 25px;"><h4 class="">Tips</h4></div>
                    </v-flex>
                    <v-flex xs8 class="text-xs-center white--text mx-2 mt-2">
                      <div class="py-1 px-2" style="min-height: 19px; background-color: #808080; border-radius: 10px;"><h4 style="font-weight: 300;" class="">{{ props.item.notes }}</h4></div>
                    </v-flex>
                    <v-spacer/>
                  </v-layout>



                </v-card>
              </template>

            </v-data-table>


            <v-flex lg1> </v-flex>
          </v-flex>
          </v-layout>

        </v-slide-y-transition>
      </v-container>
      <v-layout row>
        <v-spacer/>
        <v-flex xs2 class="text-xs-right" style="position: relative;">
          <img :src="'static/img/bananaisu.png'" style="position:absolute; bottom: 0; right: 0; z-index: 0 !important; opacity: 0.5;"/>
        </v-flex>
      </v-layout>
    </v-content>
  </v-app>
</template>

<script>
  import axios from 'axios'

  export default {
    data () {
      return {
        leftDrawer: false,
        drawer: false,
        apiError: false,
        apiErrorResponse: "",
        loadingData: true,
        loaded: false,
        imageDialog: {},
        aboutDialog: false,
        items: [
        ],
        right: true,
        rightDrawer: false,
        title: 'Starlight -Re LIVE- DB βananaisu',
        pictureToDisplay: "",
        filteredTable: [],
        showFilterBadge: false,
        hover: {
          element: true,
          flower: false,
          wind: false,
          snow: false,
          cloud: false,
          moon: false,
          cosmos: false,
          dream: false,
          school: true,
          seisho: false,
          rinmeikan: false,
          frontier: false,
          seekfelt: false
        },
        allFiltersOff: true,
        characterToFilter: null,
        searchFilter: "",
        nameslist: {},
        filters:
          {
          star2Filter: false,
          star3Filter: false,
          star4Filter: false,
          normalFilter: false,
          specialFilter: false,
          flowerFilter: false,
          windFilter: false,
          snowFilter: false,
          cloudFilter: false,
          moonFilter: false,
          cosmosFilter: false,
          dreamFilter: false,
          frontFilter: false,
          middleFilter: false,
          backFilter: false,
          seishoFilter: false,
          seekfeltFilter: false,
          frontierFilter: false,
          rinmeikanFilter: false,
          characterFilter: false
        },
        headers: [
          { text: 'Rarity', value: 'rarity', align: 'left', width:'1%', class: 'rarityHeader'},
          { text: 'Name', value: 'subname', align: 'left'},
          { text: 'School', value: 'school', align: 'left'},
          { text: 'Element', value: 'element', align: 'left'},
          { text: 'Type', value: 'atktype', align: 'left'},
          { text: 'Position', value: 'position', align: 'left'},
          { text: 'Overall', value: 'overall', align: 'left'},
          { text: 'HP', value: 'hp', align: 'left' },
          { text: 'ATK', value: 'atk', align: 'left' },
          { text: 'DEF', value: 'def', align: 'left' },
          { text: 'MDEF', value: 'mdef', align: 'left' },
          { text: 'SPD', value: 'spd', align: 'left'},
        ],
        characterNames: [
          "Aijou Karen",
          "Kagura Hikari",
          "Tsuyuzaki Mahiru",
          "Saijou Claudine",
          "Tendou Maya",
          "Hoshimi Junna",
          "Daiba Nana",
          "Isurugi Futaba",
          "Hanayagi Kaoruko",
          "Tomoe Tamao",
          "Otonashi Ichie",
          "Akikaze Rui",
          "Yumeoji Fumi",
          "Tanaka Yuyuko",
          "Ootsuki Aruru",
          "Kano Misora",
          "Nonomiya Lalafin",
          "Ebisu Tsukasa",
          "Kochou Shizuha",
          "Yukishiro Akira",
          "Ootori Michiru",
          "Liu Meifan",
          "Tsuruhime Yachiyo",
          "Yumeoji Shiori"
        ],
        charadata: []
      }
    },
    methods: {
      checkAllFiltersOff: function() {
        for (var i in this.filters) {
          if (this.filters[i]) {
            return false;
          }
        }
        if (this.searchFilter != "") {
          return false;
        }
        return true;
      },
      resetFilters: function() {
        for (var i in this.filters) {
          this.filters[i] = false;
        }
        this.searchFilter = "";
        this.filteredTable = this.charadata;
        this.showFilterBadge = false;
      },
      filterAtktype: function() {
        var filteredArray = {};
        for (let i of this.charadata) {
          if (this.filters.normalFilter) {
            if (i.atktype == "normal") {
              filteredArray[i.ID]= i;
            }
          }
          if (this.filters.specialFilter) {
            if (i.atktype == "special") {
              filteredArray[i.ID]= i;
            }
          }
        }
        return filteredArray;
      },
      filterElement: function() {
        var filteredArray = {};
        for (let i of this.charadata) {
          if (this.filters.flowerFilter) {
              if (i.element == "flower") {
                filteredArray[i.ID]= i;
              }
          }
          if (this.filters.windFilter) {
              if (i.element == "wind") {
                filteredArray[i.ID]= i;
              }
          }
          if (this.filters.snowFilter) {
              if (i.element == "snow") {
                filteredArray[i.ID]= i;
              }
          }
          if (this.filters.cloudFilter) {
              if (i.element == "cloud") {
                filteredArray[i.ID]= i;
              }
          }
          if (this.filters.moonFilter) {
              if (i.element == "moon") {
                filteredArray[i.ID]= i;
              }
          }
          if (this.filters.cosmosFilter) {
              if (i.element == "cosmos") {
                filteredArray[i.ID]= i;
              }
          }
          if (this.filters.dreamFilter) {
              if (i.element == "dream") {
                filteredArray[i.ID]= i;
              }
          }
        }
        return filteredArray;
      },
      filterPosition: function() {
        var filteredArray = {};

        for (let i of this.charadata) {

          if (this.filters.frontFilter) {
            if (i.position == "front") {
              filteredArray[i.ID]= i;
            }
          }
          if (this.filters.middleFilter) {
            if (i.position == "middle") {
              filteredArray[i.ID]= i;
            }
          }
          if (this.filters.backFilter) {
            if (i.position == "back") {
              filteredArray[i.ID]= i;
            }
          }
        }
        return filteredArray;
      },
      filterRarity: function() {
        var filteredArray = {};

        for (let i of this.charadata) {

          if (this.filters.star2Filter) {
            if (i.rarity == "2") {
              filteredArray[i.ID]= i;
            }
          }
          if (this.filters.star3Filter) {
            if (i.rarity == "3") {
              filteredArray[i.ID]= i;
            }
          }
          if (this.filters.star4Filter) {
            if (i.rarity == "4") {
              filteredArray[i.ID]= i;
            }
          }
        }
        return filteredArray;
      },
      filterSchool: function() {
        var filteredArray = {};

        for (let i of this.charadata) {

          if (this.filters.seishoFilter) {
            if (i.school == "seisho") {
              filteredArray[i.ID]= i;
            }
          }
          if (this.filters.rinmeikanFilter) {
            if (i.school == "rinmeikan") {
              filteredArray[i.ID]= i;
            }
          }
          if (this.filters.frontierFilter) {
            if (i.school == "frontier") {
              filteredArray[i.ID]= i;
            }
          }
          if (this.filters.seekfeltFilter) {
            if (i.school == "seekfelt") {
              filteredArray[i.ID]= i;
            }
          }
        }
        return filteredArray;
      },
      filterByName: function() {
        var filteredArray = {};

        for (let i of this.charadata) {
          if (this.filters.characterFilter) {
            if (i.charaname == this.characterToFilter) {
              filteredArray[i.ID]= i;
            }
          }
        }
        return filteredArray;
      },
      filteredBySearch: function() {
        var temp;
        var result = {};

        if (this.searchFilter != "") {
          temp = this.charadata.filter(i => {
            return i.subname.toLowerCase().includes(this.searchFilter.toLowerCase())
          });
          for (let i of temp) {
                result[i.ID]= i;
          }

          temp = this.charadata.filter(i => {
            return i.charaname.toLowerCase().includes(this.searchFilter.toLowerCase())
          });
          for (let i of temp) {
                result[i.ID]= i;
          }

          return result;
        } else {
          for (let i of this.charadata) {
                result[i.ID]= i;
          }
          return result;
        }

      },
      handleSelect: function() {
        if (this.characterToFilter !== undefined) {
          this.filters.characterFilter = true;
        } else {
          this.filters.characterFilter = false;
        }
      },
      intersection: function(input) {
      	var result = [];
        var lists = input;
        var stop = lists.length;

          for (let i in lists[0]) {
            var existsInAll = true;
            for (var j = 1; j < lists.length; j++) {
              if (!(i in lists[j])) {
                existsInAll = false;
                break;
              }
            }
            if (existsInAll) {
              result.push(lists[0][i]);
            }
          }
          return result;
        },
      getChardata: function() {
        axios
        .get('https://92r3xgypt5.execute-api.us-east-2.amazonaws.com/dev/database')
        .then(response => {
          console.log(response.data.Items)
          this.charadata = response.data.Items;
          this.filteredTable = this.charadata;
          this.loadingData = false;
          this.loaded = true;
        })
        .catch(error => {
          console.log(error)
          this.apiError = true;
          this.apiErrorResponse = error;
        })
      },
      viewImage: function(input) {

      },
      nameToID: function(input) {
        return result;
      }
    },
    computed: {
      filterCharadata: function() {

        var stuffToFilter = [];

        if (this.filters.star2Filter || this.filters.star3Filter || this.filters.star4Filter) {
          stuffToFilter.push(this.filterRarity());
        }

        if (this.filters.normalFilter || this.filters.specialFilter) {
          stuffToFilter.push(this.filterAtktype());
        }

        if (this.filters.flowerFilter || this.filters.windFilter || this.filters.snowFilter || this.filters.cloudFilter || this.filters.moonFilter || this.filters.cosmosFilter || this.filters.dreamFilter) {

          stuffToFilter.push(this.filterElement());
        }

        if (this.filters.frontFilter || this.filters.middleFilter || this.filters.backFilter) {
          stuffToFilter.push(this.filterPosition());
        }

        if (this.filters.seishoFilter || this.filters.seekfeltFilter || this.filters.frontierFilter || this.filters.rinmeikanFilter) {
          stuffToFilter.push(this.filterSchool());
        }


        if (this.filters.characterFilter) {

          stuffToFilter.push(this.filterByName());
        }

        if (this.searchFilter != "") {
          stuffToFilter.push(this.filteredBySearch());
        }

        this.filteredTable = this.intersection(stuffToFilter);

        if (this.checkAllFiltersOff()) {
          this.filteredTable = this.charadata;
          this.showFilterBadge = false;
        } else {
          this.showFilterBadge = true;
        }
      }
    },
    created(){
      if (!this.loaded) {
        this.getChardata()
      }
    }
  }
</script>

<style>
@import "https://cdn.materialdesignicons.com/2.8.94/css/materialdesignicons.min.css";

table.v-table, tbody td:first-child, table.v-table tbody td:not(:first-child), table.v-table tbody th:first-child, table.v-table tbody th:not(:first-child), table.v-table thead td:first-child, table.v-table thead td:not(:first-child), table.v-table thead th:first-child, table.v-table thead th:not(:first-child) {
  padding: 0 5px;
}

.v-btn {
  min-width: 0;
  max-height: 200;
}

.rarityHeader {
  padding-left: 25px !important;
}

.rounded-card {
  border-radius: 100px;
}
</style>



